.product-listing-page {
  min-height: 100vh;
  background-color: #f8f9fa;
}

.product-listing-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 2rem;
}

.page-title {
  font-size: 2.5rem;
  color: #2d5016;
  text-align: center;
  margin: 0 0 3rem 0;
  font-weight: 700;
}

.category-section {
  margin-bottom: 4rem;
}

.category-title {
  font-size: 2rem;
  color: #2d5016;
  margin: 0 0 1.5rem 0;
  font-weight: 600;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid #2d5016;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
}

@media (max-width: 768px) {
  .product-listing-container {
    padding: 2rem 1rem;
  }

  .page-title {
    font-size: 2rem;
  }

  .category-title {
    font-size: 1.5rem;
  }

  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .products-grid {
    grid-template-columns: 1fr;
  }
}
