.landing-page {
  min-height: 100vh;
  background-image: linear-gradient(rgba(45, 80, 22, 0.7), rgba(45, 80, 22, 0.7)),
    url('https://images.unsplash.com/photo-1466692476868-aef1dfb1e735?w=1920&h=1080&fit=crop');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.landing-content {
  max-width: 800px;
  text-align: center;
}

.landing-text {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 3rem;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
}

.company-name {
  font-size: 3.5rem;
  color: #2d5016;
  margin: 0 0 1.5rem 0;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.company-description {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #333;
  margin: 0 0 2.5rem 0;
  text-align: justify;
}

.get-started-btn {
  display: inline-block;
  background-color: #2d5016;
  color: white;
  text-decoration: none;
  padding: 1rem 3rem;
  border-radius: 50px;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(45, 80, 22, 0.3);
}

.get-started-btn:hover {
  background-color: #3d6b1f;
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(45, 80, 22, 0.4);
}

@media (max-width: 768px) {
  .landing-page {
    padding: 1rem;
  }

  .landing-text {
    padding: 2rem;
  }

  .company-name {
    font-size: 2.5rem;
  }

  .company-description {
    font-size: 1rem;
    text-align: left;
  }

  .get-started-btn {
    font-size: 1.1rem;
    padding: 0.9rem 2.5rem;
  }
}
