.product-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15);
}

.product-image-container {
  width: 100%;
  height: 250px;
  overflow: hidden;
  background-color: #f5f5f5;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-info {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.product-name {
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 0.5rem 0;
  color: #2d5016;
}

.product-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #27ae60;
  margin: 0 0 0.8rem 0;
}

.product-description {
  font-size: 0.9rem;
  color: #666;
  margin: 0 0 1rem 0;
  line-height: 1.5;
  flex-grow: 1;
}

.add-to-cart-btn {
  background-color: #2d5016;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease, opacity 0.3s ease;
  width: 100%;
}

.add-to-cart-btn:hover:not(.disabled) {
  background-color: #3d6b1f;
}

.add-to-cart-btn.disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .product-image-container {
    height: 200px;
  }

  .product-info {
    padding: 1rem;
  }

  .product-name {
    font-size: 1.1rem;
  }

  .product-price {
    font-size: 1.3rem;
  }
}
